<template>
    <div class="card-page">
        <div class="page-head">
            <a class="l">返回</a>
            <a>我的福卡</a>
            <a class="r">福卡榜</a>
        </div>
        <div class="page-title">
            <h2>集齐五福，拼手气分5亿</h2>
            <p>69,897,865人已集齐，2月4号22:18开奖</p>
        </div>
        <div class="page-body">
            <div id="cardSwiper" class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(item, index) in list" :key="index">
                        <div class="main-card">
                            <transition name="filid">
                                <div class="card-front" v-if="!item['show_after']" key="front">
                                    <div class="c-image"><img src="../assets/images/ggg.jpg"></div>
                                    <div class="c-body">
                                        <a class="fff" v-if="item['card_type'] == 'aiguo'">爱国福</a>
                                        <a class="fff" v-if="item['card_type'] == 'fuqiang'">富强福</a>
                                        <a class="fff" v-if="item['card_type'] == 'hexie'">和谐福</a>
                                        <a class="fff" v-if="item['card_type'] == 'youshan'">友善福</a>
                                        <a class="fff" v-if="item['card_type'] == 'jingye'">敬业福</a>
                                        <p class="c-text">支付宝祝你新年快乐</p>
                                        <div class="c-fuzi"><img src="../assets/images/fu.jpg"></div>
                                        <a class="sz">{{cardInfo(item)}}</a>
                                        <a class="fz" @click="showCardafter(index)">翻转</a>
                                    </div>
                                    <div class="c-p-logo"><i></i></div>
                                </div>
                            </transition>
                            <transition name="filid">
                                <div class="card-after" v-if="item['show_after']" key="after">
                                    <div class="c-image"><img src="../assets/images/ggg.jpg"></div>
                                    <div class="c-body">
                                        <a class="sz">{{cardInfo(item)}}</a>
                                        <a class="fz" @click="showCardafter(index)">翻转</a>
                                    </div>
                                </div>
                            </transition>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="page-foot">
            <div class="hide-scroll">
                <div class="nav-list">
                    <div class="nav-item" v-for="(item, index) in navlist" @click="toCard(item.code)" :key="index">
                        <i>{{getCount(item.code)}}</i>
                        <span class="tx">{{item.text}}</span>
                        <span class="bg"></span>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import 'swiper/dist/css/swiper.min.css'
import Swiper from 'swiper/dist/js/swiper.min.js'
export default {
    data () {
        return {
            cardSwiper: null,
            navlist: [{
                text: '爱国福',
                code: 'aiguo'
            }, {
                text: '富强福',
                code: 'fuqiang'
            }, {
                text: '和谐福',
                code: 'hexie'
            }, {
                text: '友善福',
                code: 'youshan'
            }, {
                text: '敬业福',
                code: 'jingye'
            }],
            list: [{
                show_after: false,
                card_type: 'aiguo'
            }, {
                show_after: false,
                card_type: 'fuqiang'
            }, {
                show_after: false,
                card_type: 'fuqiang'
            }, {
                show_after: false,
                card_type: 'fuqiang'
            }, {
                show_after: false,
                card_type: 'hexie'
            }, {
                show_after: false,
                card_type: 'hexie'
            }, {
                show_after: false,
                card_type: 'youshan'
            }, {
                show_after: false,
                card_type: 'jingye'
            }, {
                show_after: false,
                card_type: 'jingye'
            }]
        }
    },
    methods: {
        toCard (code) {
            var activeIndex = this.cardSwiper.activeIndex
            for (let i = 0; i < this.list.length; i++) {
                if (this.list[i]['card_type'] === code) {
                    var ss = activeIndex > i ? activeIndex - i : i - activeIndex
                    this.cardSwiper.slideTo(i, ss * 200, false)
                    break
                }
            }
        },
        showCardafter (index) {
            this.list[index]['show_after'] = !this.list[index]['show_after']
        },
        getCount (code) {
            var sss = 0
            for (let i = 0; i < this.list.length; i++) {
                if (this.list[i]['card_type'] === code) {
                    sss++
                }
            }
            return sss
        },
        cardInfo (item) {
            var cIndex = 0
            var cArray = []
            for (let i = 0; i < this.list.length; i++) {
                var card = this.list[i]
                if (item['card_type'] === card['card_type']) {
                    cArray.push(card)
                }
            }
            for (let j = 0; j < cArray.length; j++) {
                var cItem = cArray[j]
                if (cItem === item) {
                    cIndex = j + 1
                }
            }
            return `${cIndex}/${cArray.length}`
        }
    },
    mounted () {
        this.cardSwiper = new Swiper('#cardSwiper', {
            slidesPerView: 'auto',
            centeredSlides: true,
            watchSlidesProgress: true,
            paginationClickable: true,
            onProgress: function (a) {
                for (var i = 0; i < a.slides.length; i++) {
                    var c = a.slides[i]
                    var d = c.progress
                    var es = c.style
                    console.log(this.index)
                    var sss = 1 - Math.min(Math.abs(d / 1.2), 1);
                    es.opacity = sss > 0.2 ? sss : 0.2
                    es.webkitTransform = es.MsTransform = es.msTransform = es.MozTransform = es.OTransform = es.transform = `translate3d(0px, 0, ${-Math.abs(150 * d)}px)`
                }
            },
            onSetTransition: function (a, b) {
                for (var c = 0; c < a.slides.length; c++) {
                    var es = a.slides[c].style
                    es.webkitTransitionDuration = es.MsTransitionDuration = es.msTransitionDuration = es.MozTransitionDuration = es.OTransitionDuration = es.transitionDuration = b + 'ms'
                }
            }
        })
    }
}
</script>

<style lang="scss">
    body {
        background: #212039;
    }
</style>
